"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Play, RotateCcw, Settings, Zap, Brain, FileText } from "lucide-react";

interface AIModel {
  id: string;
  name: string;
  description: string;
  capabilities: string[];
}

export default function InteractiveAIPlayground() {
  const [selectedModel, setSelectedModel] = useState("gpt-4");
  const [temperature, setTemperature] = useState([0.7]);
  const [maxTokens, setMaxTokens] = useState([150]);
  const [prompt, setPrompt] = useState("");
  const [output, setOutput] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);

  const models: AIModel[] = [
    {
      id: "gpt-4",
      name: "GPT-4",
      description: "Most capable model for complex reasoning",
      capabilities: ["Text", "Code", "Analysis", "Creative"],
    },
    {
      id: "claude-3",
      name: "Claude 3",
      description: "Excellent for analysis and long-form content",
      capabilities: ["Text", "Analysis", "Research", "Writing"],
    },
    {
      id: "gemini-pro",
      name: "Gemini Pro",
      description: "Google's multimodal AI model",
      capabilities: ["Text", "Code", "Math", "Multimodal"],
    },
    {
      id: "llama-2",
      name: "Llama 2",
      description: "Open-source model for various tasks",
      capabilities: ["Text", "Code", "Chat", "Reasoning"],
    },
  ];

  const handleGenerate = async () => {
    if (!prompt.trim()) return;

    setIsGenerating(true);
    setOutput("");

    // Simulate AI generation with typing effect
    const responses = [
      "This is a simulated response from the AI model. The actual implementation would connect to the selected AI model with the specified parameters.",
      "Based on your prompt and the current settings, here's what the AI would generate. Temperature affects creativity, while max tokens controls length.",
      "The AI playground allows you to experiment with different models and parameters to see how they affect the output quality and style.",
    ];

    const selectedResponse =
      responses[Math.floor(Math.random() * responses.length)];

    // Typing effect
    for (let i = 0; i <= selectedResponse.length; i++) {
      await new Promise((resolve) => setTimeout(resolve, 30));
      setOutput(selectedResponse.slice(0, i));
    }

    setIsGenerating(false);
  };

  const handleReset = () => {
    setPrompt("");
    setOutput("");
    setTemperature([0.7]);
    setMaxTokens([150]);
  };

  return (
    <div className="max-w-6xl mx-auto grid lg:grid-cols-3 gap-6">
      {/* Controls Panel */}
      <div className="lg:col-span-1 space-y-6">
        <Card className="p-6 border-0 bg-white/60 backdrop-blur-md shadow-xl">
          <div className="flex items-center space-x-2 mb-4">
            <Settings className="h-5 w-5 text-sky-600" />
            <h3 className="text-lg font-semibold">Model Settings</h3>
          </div>

          <div className="space-y-4">
            {/* Model Selection */}
            <div>
              <Label className="text-sm font-medium mb-2 block">AI Model</Label>
              <Select value={selectedModel} onValueChange={setSelectedModel}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {models.map((model) => (
                    <SelectItem key={model.id} value={model.id}>
                      <div>
                        <div className="font-medium">{model.name}</div>
                        <div className="text-xs text-gray-500">
                          {model.description}
                        </div>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <div className="flex flex-wrap gap-1 mt-2">
                {models
                  .find((m) => m.id === selectedModel)
                  ?.capabilities.map((cap) => (
                    <Badge key={cap} variant="secondary" className="text-xs">
                      {cap}
                    </Badge>
                  ))}
              </div>
            </div>

            {/* Temperature */}
            <div>
              <Label className="text-sm font-medium mb-2 block">
                Temperature: {temperature[0]}
              </Label>
              <Slider
                value={temperature}
                onValueChange={setTemperature}
                max={2}
                min={0}
                step={0.1}
                className="w-full"
              />
              <p className="text-xs text-gray-500 mt-1">
                Controls randomness (0 = focused, 2 = creative)
              </p>
            </div>

            {/* Max Tokens */}
            <div>
              <Label className="text-sm font-medium mb-2 block">
                Max Tokens: {maxTokens[0]}
              </Label>
              <Slider
                value={maxTokens}
                onValueChange={setMaxTokens}
                max={500}
                min={50}
                step={10}
                className="w-full"
              />
              <p className="text-xs text-gray-500 mt-1">
                Maximum length of the response
              </p>
            </div>

            {/* Action Buttons */}
            <div className="flex space-x-2 pt-4">
              <Button
                onClick={handleGenerate}
                disabled={isGenerating || !prompt.trim()}
                className="flex-1"
              >
                {isGenerating ? (
                  <>
                    <Zap className="h-4 w-4 mr-2 animate-spin" />
                    Generating...
                  </>
                ) : (
                  <>
                    <Play className="h-4 w-4 mr-2" />
                    Generate
                  </>
                )}
              </Button>
              <Button variant="outline" onClick={handleReset}>
                <RotateCcw className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </Card>

        {/* Quick Prompts */}
        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-4">Quick Prompts</h3>
          <div className="space-y-2">
            {[
              "Write a haiku about technology",
              "Explain quantum computing simply",
              "Create a Python function",
              "Brainstorm startup ideas",
            ].map((quickPrompt) => (
              <Button
                key={quickPrompt}
                variant="outline"
                size="sm"
                className="w-full text-left justify-start text-xs"
                onClick={() => setPrompt(quickPrompt)}
              >
                {quickPrompt}
              </Button>
            ))}
          </div>
        </Card>
      </div>

      {/* Input/Output Panel */}
      <div className="lg:col-span-2 space-y-6">
        {/* Input */}
        <Card className="p-6">
          <div className="flex items-center space-x-2 mb-4">
            <FileText className="h-5 w-5 text-sky-600" />
            <h3 className="text-lg font-semibold">Your Prompt</h3>
          </div>
          <Textarea
            placeholder="Enter your prompt here... Try asking for creative writing, code generation, analysis, or any other task!"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            className="min-h-32 resize-none"
          />
          <div className="flex justify-between items-center mt-2 text-xs text-gray-500">
            <span>{prompt.length} characters</span>
            <span>Press Ctrl+Enter to generate</span>
          </div>
        </Card>

        {/* Output */}
        <Card className="p-6">
          <div className="flex items-center space-x-2 mb-4">
            <Brain className="h-5 w-5 text-sky-600" />
            <h3 className="text-lg font-semibold">AI Response</h3>
            {isGenerating && (
              <div className="ml-auto flex items-center space-x-2">
                <div className="w-2 h-2 bg-sky-500 rounded-full animate-pulse" />
                <span className="text-sm text-sky-600">Generating...</span>
              </div>
            )}
          </div>
          <div className="min-h-32 p-4 bg-gray-50 rounded-lg border">
            {output ? (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="text-gray-800 whitespace-pre-wrap"
              >
                {output}
              </motion.div>
            ) : (
              <div className="text-gray-400 italic">
                AI response will appear here...
              </div>
            )}
          </div>
          {output && (
            <div className="flex justify-between items-center mt-2 text-xs text-gray-500">
              <span>{output.length} characters generated</span>
              <span>
                Model: {models.find((m) => m.id === selectedModel)?.name}
              </span>
            </div>
          )}
        </Card>
      </div>
    </div>
  );
}
